.Background{
    width: 100%;
    height: 100%;
    position: absolute;
}
.Navbar{
    width: 100%;
    height: 40px;
}
.View{
    width: 100%;
    height: calc(100% - 40px);
}
.SideBar{
    width:500px;
    height: calc(100% - 40px);
    position: absolute;
    z-index: 100;
    left: -500;
    bottom:0;
    transition: left 200ms ease-in-out;
  opacity: 1;
}
