.Background{
    width: 100%;
    height: 40px;
    color:white;
    background-color:#6a61bd;
    display: flex;
  align-items: center;
  position:absolute;
}
.Menu{
    font-size: 20px;
    position:absolute;
    left:10px;
    font-weight: 600;
}
.Title{
    font-size: 15px;
    position:absolute;
    left:45px;
    font-weight: 600;
}
.LeftFrameButton{
    position:absolute;
    width:30px;
    left: 0;
    text-align: center;
}
.RightFrameButton{
    position:absolute;
    width:30px;
    right:0;
    text-align: center;
}
.FrameNavigationButtonGroup{
    font-size: 20px;
    position:absolute;
    right:0px;
    width:60px;
    display: flex;
  justify-content: center;
  align-items: center;
}
.FileName{
    font-size: 15px;
    font-weight: 600;
    position:absolute;
  left: 50%;
  transform: translate(-50%, 0%);
}