.Background{
    width: 100%;
    height: 100%;
    position: relative;
}
.Center{
    display: flex;
    align-items: center;
    position:absolute;
    height:100%;
}
.DragHandle{
    position:absolute;
    color:white;
    font-size: 20px;
    font-weight: 600;
    left:5px;
    
}
.Label{
    position:absolute;
    color:#ffffff;
    left:25px;
    font-weight: 600;
    font-size:2vh;
}
.HighBoundLabel{
    color:#ffffff;
    font-weight: 600;
    font-size:1vh;
    top:0;
    right:5px;
    position:absolute;
    text-align: right;
}
.LowBoundLabel{
    color:#ffffff;
    font-weight: 600;
    font-size:1vh;
    bottom:0;
    right:5px;
    position:absolute;
    text-align: right;
}
